import * as React from 'react';
import { Link } from 'react-router-dom';
import { useState, useEffect } from 'react';
import {useSelector, useDispatch} from 'react-redux';

import {GoogleLogin, GoogleLogout} from 'react-google-login';
import FacebookLogin from 'react-facebook-login';

import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


export const SignIn: React.FC<SignInProps> = props => {

    const dispatch = useDispatch();

    const responseGoogle = (response: any) => {
        console.log(response);
        if (response.error) {
            dispatch({type: "LOGGED_OUT", payload: false});
            props.hideSignInOptions();
        }
        else {
            dispatch({type: "LOGGED_IN", payload: true});
            localStorage.setItem('name', response.profileObj.name);
            localStorage.setItem('email', response.profileObj.email);
            localStorage.setItem('profilePicUrl', response.profileObj.imageUrl)
            localStorage.setItem('isAuthenticated', 'true'); //still have to add local storage key
            props.hideSignInOptions();
        }
    }

    const responseFacebook = (response: any) => {
        if (!response.accessToken) {
            dispatch({type: "LOGGED_OUT", payload: false});
            props.hideSignInOptions();
        }
        else {
            dispatch({type: "LOGGED_IN", payload: true});
            localStorage.setItem('name', response.name);
            localStorage.setItem('email', response.email);
            localStorage.setItem('profilePicUrl', response.picture.data.url);
            localStorage.setItem('isAuthenticated', 'true'); //still have to add local storage key
            props.hideSignInOptions();
        }
        console.log(response);
    }

   return (
    <React.Fragment>
        <div className={"sign-in_wrapper " + props.className} >
            <div className="sign-in_left-side"></div>
            <div className="sign-in_right-side">
                <div className="sign-in_title">
                    By having a Bukt account, you can save bucket list items, share you accomplishments, and earn our famous stickers!
                </div>
                <div className="sign-in_social-media_wrapper">
                    <GoogleLogin
                        clientId="259196010434-f28kmgrtasd73khgu7glccdo8i9s6o6u.apps.googleusercontent.com"
                        buttonText="Sign In With Google"
                        onSuccess={responseGoogle}
                        onFailure={responseGoogle}
                        isSignedIn={false}
                        cookiePolicy={'single_host_origin'}
                        className="google_login"
                    />
                    <FacebookLogin
                        appId="799765990850479"
                        autoLoad={false}
                        fields="name,email,picture"
                        callback={responseFacebook} 
                        icon="fa-facebook"
                        cssClass="facebook_login"
                    />
                </div>
                <div className="sign-in_create-account_link">
                Don't have an account? <Link to="/sign-up">Sign up here</Link>
                </div>
            </div>
            <div className="close_btn" onClick={() => props.hideSignInOptions()}><FontAwesomeIcon icon={faTimes}/></div>
        </div>
        <div className={"pop-up_background " + props.className} onClick={() => props.hideSignInOptions()}/>
    </React.Fragment>
   )
}

interface SignInProps {
    className?: string;
    isLoggedIn?: any;
    hideSignInOptions?: any;
}
