import * as React from 'react';
import { useState, useEffect, useLayoutEffect } from 'react';
import { Link } from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import { AppState } from 'store/configureStore';
import { SignIn } from './sign_in.component';
import * as Logo from "../assets/red-bucket-icon.jpg";
import "../styles/_navbar.scss";

export const NavBar: React.FC<NavBarProps> = props => {

    const [ signInForm, toggleSignInForm ] = useState<string>("hide");
    const isLogged = useSelector((state: AppState) => state.isLogged); //to get state
    const dispatch = useDispatch();
    
    const handleLogout = () => {
        dispatch({type: "LOGGED_OUT", payload: false});
        localStorage.clear();
    }
    const showSignInOptions= () => {
        toggleSignInForm("");
    }

    useLayoutEffect(() => {
        console.log('layout effect called');
        if (localStorage.getItem('isAuthenticated') == "true") { //Will replace this with JWT verification
            dispatch({type: "LOGGED_IN", payload: true});
        }
        else {
            dispatch({type: "LOGGED_OUT", payload: false});
        }
    }, []);

    return (
        <div className="navbar_wrapper">
            <div className="navbar">
                <ul className="navbar_left">
                    <li><Link to="/">Explore</Link></li>
                    <li><Link to="/">Ideas</Link></li>
                    <li><Link to="/">Search</Link></li>
                </ul>
                <Link  className="navbar_center" to ="/"><img className="navbar_logo" alt="logo" src={String(Logo)}/> </Link>
                <ul className="navbar_right">
                    {isLogged === true ? (
                        <React.Fragment> 
                            <li><Link to="/" >Bucket List</Link></li>
                            <li><Link to="/" >About</Link></li>
                            <li><Link to="/" onClick={handleLogout}>Logout</Link></li>
                        </React.Fragment>
                    ) 
                    : 
                    (
                        <React.Fragment>
                            <li><Link to="/" >About</Link></li>
                            <li><Link to="/" >Contact</Link></li>
                            <li><Link to="/" onClick={showSignInOptions}>Sign In</Link></li>
                        </React.Fragment>
                    )}
                    <SignIn className={signInForm} hideSignInOptions={() => toggleSignInForm("hide")}/>
                </ul>
            </div>
        </div>
    )
}

interface NavBarProps {}
