import { LoggedInActionTypes } from "types/actions";

const loggedReducer = (state : boolean = false, action: LoggedInActionTypes): boolean => {
    switch(action.type) {
        case 'LOGGED_IN' :
            return true;
        case 'LOGGED_OUT' :
            return false;
        default:
            return state;
    }
}

export {loggedReducer};